@import "variables";

button {
    background-color: $black; 
    border: none;
    color: white;
    font-weight: 600;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 2px;
    cursor: pointer;
    font-size: 16px;
}